import { expect } from "chai";
import testUtils from "./utils";
import {initDB, INIT_DB} from '../src/store/db'
import {Map, fromJS} from 'immutable'

describe("application launch", () => {
  beforeEach(testUtils.beforeEach);
  afterEach(testUtils.afterEach);

  it("shows hello world text on screen after launch", function() {
    return this.app.client.getText("#greet").then(text => {
      expect(text).to.equal("Let's Migrate!");
    });
  });

});

describe('actions', () => {
  it('should create an action to add a db', () => {
    const db = fromJS({users: {name:{type: 'string', allowNull: false}}})
    const expectedAction = Map({
      type: INIT_DB,
      db
    })
    console.log(initDB(db))
    expect(expectedAction.equals(initDB(db))).to.equal(true)
  })
})



