const {Map} = require('immutable')
const diff = require('immutablediff')

// actions
export const INIT_DB = 'INIT_DB'

export const initDB = (db) => ({type: INIT_DB, db})

// initial state

const initialDB = Map({})

// reducer

export default function (state = initialDB, action) {
  switch (action.type) {
    case INIT_DB:
      return action.db
    default:
      return state
  }
}



